.table {
  width: 100%;
  max-width: 100%;
  border-collapse: collapse;
  margin: 0;

  overflow-x: auto;

  &__header {
    background-color: #f5feff;
    @include for-size(phone-only) {
      display: none;
    }
  }
  &__body {
    & .table__row {
      border-bottom: 1px solid $border-base;
    }
  }
  &__row {
    position: relative;
    max-width: 100%;

    @include for-size(phone-only) {
      display: flex;
      flex-direction: column;
      padding: $padding-and-margin;
    }
  }
  &__title {
    padding: $padding-and-margin;
    color: #808080;
  }
  &__field {
    position: relative;
    padding: $padding-and-margin;
    padding-bottom: calc(var(--padding-and-margin) * 1.5);
    vertical-align: top;
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;

    @include for-size(phone-only) {
      width: 100%;
      max-width: 12.5rem;
      padding: $padding-and-margin 0;
      font-size: 80%;

      &::before {
        position: absolute;
        content: attr(data-name);
        top: 0;
        left: 0;
        color: #808080;
      }
    }

    & a {
      word-break: break-all;
    }
  }
}
