/** Custom Select **/
.custom-select-wrapper {
  position: relative;
  // display: inline-block;
  user-select: none;
  width: 100%;
  background-color: $default-color;
}
.custom-select-wrapper select {
  display: none;
}
.custom-select {
  position: relative;
  // display: inline-block;

  &.page-form__field {
    padding: calc(var(--padding-and-margin) / 3)
      var(--padding-and-margin);
  }
}
.custom-select-trigger {
  position: relative;
  display: block;
  width: 100%;
  // width: 130px;
  // padding: 0 84px 0 22px;
  // font-size: 22px;
  // font-weight: 300;
  // color: #fff;
  // line-height: 60px;
  // background: #5c9cd8;
  // border-radius: 4px;
  cursor: pointer;
}
.custom-select-trigger:after {
  position: absolute;
  display: block;
  content: "";
  width: 10px;
  height: 10px;
  top: 50%;
  right: 0;
  margin-top: 0;
  border-bottom: 1px solid $border-base;
  border-right: 1px solid $border-base;
  transform: rotate(45deg) translateY(-50%);
  transition: all 0.4s ease-in-out;
  transform-origin: 50% 0;
}
.custom-select.opened .custom-select-trigger:after {
  margin-top: 3px;
  transform: rotate(-135deg) translateY(-50%);
}
.custom-options {
  z-index: 10;
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  min-width: 100%;
  max-height: 160px;
  margin: 0;
  border: 1px solid $border-base;
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.07);
  background: #fff;
  transition: all 0.4s ease-in-out;

  overflow-y: auto;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-15px);
}
.custom-select.opened .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}
