.option-hover:before {
  background: #f9f9f9;
}
.custom-option {
  position: relative;
  display: block;
  padding: calc(var(--padding-and-margin) / 4);
  padding-left: calc(var(--padding-and-margin) / 1);
  // padding: 0 22px;
  font-size: 80%;
  border-bottom: 1px solid $border-base;
  // font-size: 18px;
  // font-weight: 600;
  // color: $border-base;
  // line-height: 47px;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}
.custom-option:first-of-type {
  border-radius: 4px 4px 0 0;
}
.custom-option:last-of-type {
  border-bottom: 0;
  border-radius: 0 0 4px 4px;
}
.custom-option:hover,
.custom-option.selection {
  background: #f9f9f9;
}
