.popup {
  // background: #fff;
  position: relative;

  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;

  @include for-size(tablet-landscape-up) {
    max-width: 45rem;
    max-height: 40rem;

    align-items: center;
    justify-content: center;
  }

  &__wrapper {
    padding: $padding-and-margin;
    margin-top: auto;
    margin-bottom: auto;
    width: 100%;
    background-color: $bg-color;
    border-radius: 5px;

    @include for-size(phone-only) {
      height: 100%;
      min-height: 100vh;
      border-radius: 0;
    }
  }
  &__header {
    position: relative;
  }
}

.popup-overlay {
  display: none;
  z-index: 1001;
  position: fixed;
  bottom: 0;
  left: 0;
  top: 0;
  right: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.5);

  // background-image: url("../img/bg/bg-1.png");
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 50%;

  // opacity: 0;

  animation-name: close-opacity;
  animation-duration: 1s;
}

.popup-overlay > * {
  transform: translateZ(0px);
}

.popup-show {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  animation-name: open-opacity;
  animation-duration: 1s;

  // opacity: 1;
  // transition: all 0.5s ease-in-out;
}

.popup-close {
  position: absolute;
  top: 0;
  right: 0;
  & svg {
    & rect {
      &:first-child {
        transform: rotate(45deg) translateX(1.5px);
      }
      &:last-child {
        transform: rotate(-45deg) translateX(-5.5px);
      }
    }
  }
}
