.link {
  display: inline;
  vertical-align: middle;
  background-color: transparent;
  line-height: normal;
  color: $link-color;
  border-radius: 0;
  text-decoration: none;
  transition: 0.4s;

  &:focus,
  &:hover {
    text-decoration: none;
    color: $primary-color;
  }

  &--uppercase {
    text-transform: uppercase;
  }

  &--btn {
    padding: $btn-padding;
    background-color: $btn-bg-color;
    color: $btn-tex-color;
    font-family: $mainFont-bold;

    border-radius: 0.3125rem;

    &:focus,
    &:hover {
      color: $default-color;
      text-decoration: none;
      background-color: darker($btn-bg-color, 2.5%);
    }
  }

  &--button {
    padding: $btn-padding;
    @include ff("font-size", $mainTitleh2);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    background-color: $btn-bg-color;
    color: $btn-tex-color;
    border-radius: 0.3125rem;

    &:focus,
    &:hover {
      color: $btn-tex-color;
      background-color: darker($btn-bg-color, 2.5%);
    }
  }
  &--primary {
    font-weight: 600;
    text-transform: uppercase;
    color: $default-color;
    background-color: $primary-color;

    &:focus,
    &:hover {
      text-decoration: none;
      color: $default-color;
      background-color: darker($primary-color, 2.5%);
    }
  }
  &--white {
    font-weight: 600;
    text-transform: uppercase;
    color: $primary-color;
    background-color: $default-color;
    border: 2px solid $primary-color;

    &:focus,
    &:hover {
      text-decoration: none;
      color: darker($primary-color, 2.5%);
      background-color: $default-color;
      border-color: darker($primary-color, 2.5%);
    }
  }
}
