.breadcrumb {
  margin-top: calc(var(--padding-and-margin) * 2);
  margin-bottom: calc(var(--padding-and-margin) * 2);
  font-size: 1.0625rem;

  & a {
    position: relative;
    color: $link-color;
    text-decoration: none;

    &:not(:last-child) {
      padding-right: 1.25rem;
    }

    &:not(:last-child)::before {
      content: "/";
      position: absolute;
      top: 50%;
      right: 5px;
      transform: translateY(-50%);
      color: $breadcrumb-color;
    }

    &:last-child {
      color: $breadcrumb-color;
    }
  }
}
