@keyframes open-opacity {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes close-opacity {
  0% {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
@keyframes open-height {
  0% {
    height: 0;
  }

  100% {
    height: 100%;
  }
}
@keyframes close-height {
  0% {
    height: 100%;
  }

  100% {
    height: 0;
  }
}
