.container {
  max-width: 80rem;
  padding: 0 15px;
  margin: 0 auto;
  &--center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
}
.container-fluid {
  padding: 0 15px;
  margin: 0 auto;
  &--center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
}
