.tabs__item.active {
  opacity: 1;
  color: $default-color;
  background-color: $primary-color;
}

.tabs__panel {
  display: none;
  animation: close-opacity 1s forwards;
}

.tabs__panel.active {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  animation: open-opacity 1s forwards;
}

@media screen and (max-width: 600px) {
  .tabs {
    flex-direction: column;
  }

  .tabs__item {
    width: auto;
  }

  .tabs__item:not(:last-child) {
    margin-right: 0;
  }
}
