*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: $mainFont;
  font-size: $mainFontSize;
  font-weight: $mainFontWeight;
  line-height: $mainLineHeight;
  color: $text-color;

  scroll-behavior: smooth;

}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

// footer к низу

.wrapper {

  width: 100%;
  min-height: 100%;
}
.main {
  min-height: calc(100% - 400px);
}

.overlay {
  overflow: hidden;
  height: 100vh;
}

.scroll-content {
  overflow-y: auto;
  height: 100%;
}

.active {
  max-height: auto;
  transition: 0.5s;
  overflow: visible;
}
.closed {
  max-height: 0;
  transition: 0.5s;
  overflow: hidden;
}
