.sidemenu {
  @include listReset();
  padding: var(--padding-and-margin);
  background-color: $bg-color;

  &__item {
    margin-bottom: calc(var(--mb) / 2);
  }
  &__link {
    display: flex;
    align-items: center;

    &.link {
      display: flex;
    }

    & svg {
      width: 1.25rem;
      height: 1.25rem;
    }
  }
  &__item-name {
    padding-left: calc(var(--padding-and-margin) / 2);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
  }
}
