.button {
  display: inline-block;
  vertical-align: middle;
  padding: $btn-padding;
  @include ff('font-size', $mainTitleh2);
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  background-color: $btn-bg-color;
  color: $btn-tex-color;

  // white-space: nowrap;
  user-select: none;
  cursor: pointer;
  border: none;
  border-radius: 0.3125rem;

  outline: none;

  transition: 1s;

  &:focus,
  &:hover {
    outline: 0;

    background-color: darker($btn-bg-color, 2.5%);
  }

  &--uppercase {
    text-transform: uppercase;
  }

  &--link {
    padding: 0;
    background-color: transparent;
    color: $text-color;
    border-radius: 0;
    &:focus,
    &:hover {
      background-color: transparent;
    }
  }
  &--primary {
    font-weight: 600;
    text-transform: uppercase;
    background-color: $primary-color;

    &:focus,
    &:hover {
      outline: 0;

      background-color: darker($primary-color, 2.5%);
    }
  }
  &--tabs {
    margin: 0 0.25rem;
    @include ff('font-size', $mainTitleh2);
    color: $primary-color;
    background-color: $default-color;
    border: 2px solid $primary-color;
    border-radius: 5px;
    text-transform: uppercase;

    &:focus,
    &:hover {
      color: $default-color;
      background-color: $primary-color;
    }
  }
}
