.page-form {
  padding: $padding-and-margin;

  background-color: $bg-form-color;

  @include for-size(phone-only) {
    padding: 0.9375rem;
  }
  &__inner {
    border: none;
  }
  &__title {
    margin-bottom: calc(var(--mb) / 2);
    color: $text-form-color;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    @include listReset();

    & .page-form__list > .page-form__item {
      @include for-size(tablet-portrait-up) {
        margin-bottom: 0;
      }
    }
  }
  &__item {
    width: 100%;
    margin-bottom: var(--mb);

    @include for-size(phone-only) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    @include for-size(tablet-portrait-up) {
      width: 48%;
      &--full {
        width: 100%;
      }
    }
  }
  &__field {
    display: block;
    padding: var(--padding);

    width: 100%;
    // height: 100%;

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;

    border: 1px solid $border-base;
    border-radius: var(--border-radius);

    &:checked + .page-form__label .page-form__radio {
      --checked-color: #0075ab;
      --checked-border-color: #0075ab;
    }
    &:focus:not(:checked) + .page-form__label .page-form__radio,
    &:hover:not(:checked) + .page-form__label .page-form__radio {
      --checked-color: rgba(16, 16, 16, 0.08);

      cursor: pointer;
    }

    &:focus,
    &:hover {
      border-color: $primary-color;
      transition: 0.6s;
    }

    &::placeholder {
      font-family: $mainFont;
      font-weight: $mainFontWeight;
    }

    &:focus::-webkit-input-placeholder,
    &:active::-webkit-input-placeholder {
      // opacity: 0;
      transition: 0.6s;
      transform: translate(-12%, -12px) scale(0.8);
      padding-bottom: 5px;
    }
  }
  &__label {
    display: block;
    margin-bottom: calc(var(--mb) / 2);
    cursor: pointer;
  }
  &__comment {
    display: block;
    padding: var(--padding);
    margin-bottom: var(--mb);
    width: 100%;
    height: 6rem;
    font-family: $mainFont;
    resize: none;
    border: 1px solid $border-base;
    border-radius: var(--border-radius);

    &:focus,
    &:hover {
      border-color: $primary-color;
      transition: 0.6s;
    }

    &::placeholder {
      font-family: $mainFont;
      font-weight: $mainFontWeight;
    }

    &:focus::-webkit-input-placeholder,
    &:active::-webkit-input-placeholder {
      // opacity: 0;
      transition: 0.6s;
      transform: translate(-12%, -12px) scale(0.8);
      padding-bottom: 5px;
    }
  }
  &__radio {
    display: inline-block;
    margin-right: calc(var(--mb) / 2);
    vertical-align: middle;
    --checked-color: transparent;
    --checked-border-color: rgba(16, 16, 16, 0.08);
  }
  & .button {
    display: block;
    width: 100%;

    @include for-size(phone-only) {
      margin: auto;
    }

    &:disabled {
      color: $default-color;
      background-color: #e6e6e6;
      cursor: not-allowed;
    }
  }

  & .page-form__select {
    display: inline-block;
    width: 100%;
    // height: 34px;
    // line-height: 30px;
    position: relative;
    min-height: 2.25rem;
    font-family: $mainFont-bold;
    font-weight: 600;

    &::before {
      content: ">";
      display: inline-block;

      position: absolute;
      right: -3px;
      top: 3px;
      z-index: 2;
      width: 36px;
      height: 30px;
      text-align: center;
      line-height: 26px;
      color: $warning-color;
      background: white;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      transform: rotate(90deg);
      cursor: pointer;
    }
    & input {
      display: none;
    }
    & .page-form__select-text {
      position: absolute;
      display: block;
      top: 0;
      left: 0;

      padding: 5px 5px 5px 20px;
      width: 100%;
      height: 36px;
      font-family: $mainFont-bold;
      font-weight: 600;
    }
  }
  &__passport {
    border: none;
  }
  &__gender {
    border: none;
  }
}

.page-form__select input[name="list"]:not(:checked) ~ .page-form__select-text {
  font-family: $mainFont-bold;
  font-size: 14px;
  font-weight: 600;
  background: #ffffff;
}
.page-form__select input[name="list"]:checked ~ .page-form__select-text {
  z-index: 2;
  font-family: $mainFont-bold;
  font-weight: 600;
  color: transparent;
  background: transparent;
}
/* Стилизация выключенного селекта */

.page-form__select-close:disabled
  ~ .page-form__select-content
  .page-form__select-text {
  background: #eeeeee;
  transition: all 0.5s;
}

/* Стилизация блока с опциями. min-height сделана для фикса высоты при абсолютном позиционировании, overflow же сделан для фиксированной высоты(см. ниже) */

.page-form__select-content {
  position: absolute;
  display: block;
  padding-bottom: 0;
  min-height: 36px;

  font-family: GoogleSans-Bold;
  border: 1px solid #ddd;

  overflow: hidden;
  cursor: pointer;

  transition: all 0.5s;
}

/* Если наш селект закрыт, то он имеет высоту 36px(сделано для того, чтобы слишком большие надписи не растягивали его в высоту)  */

.page-form__select-close:not(:checked) ~ .page-form__select-content {
  height: 36px;
  transition: all 0s;
}

/* Все лейбелы(названия опций) изначально скрыты */

.page-form__select-field {
  display: none;

  margin-left: -20px;
  padding: 5px 5px 5px 20px;
  width: 100%;
  min-width: 500px;
  height: 36px;

  font-family: $mainFont-bold;
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  background: white;
  border-top: 1px solid #ddd;
}

/* Тут много объяснять не надо - просто выделение при наведении */

.page-form__select-field:hover {
  background: #eee;
  cursor: pointer;
}

/* Опять же фикс из-за абсолютного позиционирования */

.page-form__select-close:checked ~ .page-form__select-content {
  padding-top: 36px;
  width: 100%;
}

/* Если наш селект открыт, то надо сделать все опции видимыми */

.page-form__select-close:checked
  ~ .page-form__select-content
  .page-form__select-field {
  display: block;
}

/* Если какая-либо опция была выбрана, то сделать ее видимой(при выборе селект автоматически закроется) */

.page-form__select-content input:checked + .page-form__select-field {
  display: block;
  margin-top: -10px;
  border: none;
  background: white;
}

/* При открытии селекта создать подложку во весь экран, при клике на которую селект закроется, а значение останется пустым. background сделан для наглядности */

.page-form__select-close:checked ~ .page-form__select-bg {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  background: transparent;
}

.page-form__inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;

  & + label {
    position: relative;
    display: inline-block;
    padding-right: 2rem;
    font-size: 100%;
    color: #ffffff;
    background-color: transparent;

    cursor: pointer;
    &::before {
      content: "...";
      position: absolute;
      top: 50%;
      right: 0;
      padding: 0 3px;
      width: 20px;
      height: 20px;
      color: #fff;
      border: 1px solid #fff;
      line-height: 10px;
      border-radius: 3px;
      transform: translateY(-50%);
    }
  }
  & + label * {
    pointer-events: none;
  }
  &:focus + label,
  & + label:hover {
    cursor: pointer;
  }
  &:focus + label {
    outline: 1px dotted #000;
    outline: -webkit-focus-ring-color auto 5px;
  }
  &:focus + label,
  &.has-focus + label {
    outline: 1px dotted #000;
    outline: -webkit-focus-ring-color auto 5px;
  }
}
