.page {
  position: relative;
  // background-color: $bg-color;
  @include for-size(tablet-landscape-up) {
    display: flex;
    justify-content: space-between;
  }
  & .wrapper {
    width: 100%;
    margin-left: auto;
    @include for-size(tablet-portrait-up) {
      width: calc(100%);
    }
    // @include for-size(tablet-landscape-up) {
    //   width: calc(100% - 31vw);
    // }
    // @media (min-width: 950px) {
    //   width: calc(100% - 28vw);
    // }
    // @media (min-width: 1000px) {
    //   width: calc(100% - 27vw);
    // }
    // @media (min-width: 1050px) {
    //   width: calc(100% - 26vw);
    // }
    // @include for-size(desktop-up) {
    //   width: calc(100% - 22vw);
    // }
    // @media (min-width: 1300px) {
    //   width: calc(100% - 20vw);
    // }
    // @include for-size(big-desktop-up) {
    //   width: calc(100% - 20vw);
    // }
  }
  &__inner {
    padding-top: $padding-and-margin;
    margin-bottom: calc(var(--padding-and-margin) * 2);
    @include for-size(tablet-portrait-up) {
      margin-bottom: calc(var(--padding-and-margin) * 4);
    }
    @include for-size(big-desktop-up) {
      margin-bottom: 1.25rem;
    }
  }
  &__row {
    @include grid-row();
  }
  &__col {
    margin-bottom: $padding-and-margin;
    @include grid-col(
      (
        xs: 12,
        sm: 6
      )
    );
  }
  &__form-item {
    @include grid-col(
      (
        xs: 12,
        sm: 6,
        md: 4
      )
    );
  }
  &__title {
    margin-bottom: 1.5rem;
    @include ff("font-size", $mainTitleSection);
    line-height: normal;

    &--white {
      color: #ffffff;
    }
  }
  &__subtitle {
    margin-bottom: 1.5rem;
    @include ff("font-size", $mainTitleh2);
    line-height: normal;

  }
  &__title-marker {
    display: inline-block;
    margin: 0.3125rem;
    padding: 0.1875rem 0.9375rem;
    font-size: 16px;
    color: $text-color;
    opacity: 0.7;
    text-transform: lowercase;
    border-radius: 5px;
    border: 1px solid $text-color;
    word-break: break-all;
  }
  &__content {
    padding-top: 2.5rem;
  }

  // &--canvas {
  //   position: relative;
  //   overflow-x: hidden;

  //   & canvas {
  //     opacity: 0;
  //     position: absolute;
  //     width: 100%;
  //     height: auto;
  //     margin: 0;
  //     padding: 0;
  //     z-index: -1;

  //     @include for-size(tablet-landscape-up) {
  //       opacity: 1;
  //       height: 100%;
  //     }
  //   }
  // }
  & hr {
    color: #eeeeee;
    background-color: #eeeeee;
    border: none;
    height: 2px;
  }
  &__text {
    & p {
      margin-bottom: 1.5rem;
    }
    & h1,
    & h2,
    & h3,
    & h4,
    & h5,
    & h6 {
      font-size: 160%;
      line-height: normal;
      margin-bottom: 1.5rem;
    }
  }
  &__map {
    position: relative;
  }
}
