// Глобальные переменные

// Font
$mainFont: "Google Sans Regular", Arial, sans-serif;
$mainFont-bold: "Google Sans Bold", Arial, sans-serif;

// Size
$mainFontColor: #333333;
$mainFontSize: 16px;
$mainFontWeight: 400;
$mainFontWeight-bold: 600;
$mainLineHeight: 24px;

$mainTitleSection: (
  375px: 30px,
  1920px: 50px
);
$mainTitle: (
  375px: 20px,
  1920px: 25px
);
$mainTitleh1: (
  375px: 30px,
  1920px: 40px
);
$mainTitleh2: (
  375px: 16px,
  1920px: 20px
);
$main404: (
  375px: 50px,
  1920px: 100px
);
$navLink: (
  767px: 17px,
  768px: 17px,
  1280px: 15px,
  1800px: 17px
);
$cardButton: (
  375px: 18px,
  767px: 12px,
  1170px: 12px,
  1400px: 16px
);
$phoneTitle: (
  375px: 18px,
  700px: 16px,
  1600px: 25px
);

// Main color
$default-color: #ffffff;
$primary-color: #0075ab;
$secondary-color: #1f3244;
$success-color: #26de81;
$danger-color: #cc3300;
$warning-color: #fed330;
$light-color: #808080;
$purple-color: #cc0088;

$text-color: #1a1a1a;
$link-color: #1b608e;
$bg-color: #f7f7f8;
$page-title-color: #1a1a1a;
$breadcrumb-color: $light-color;
$link-color: #008dca;
$acc-color: #008dca;
$team-link-color: #008dca;

// Neutral Color
$neutral-primary: #303133;
$neutral-regular: #606266;
$neutral-secondary: #909399;
$neutral-placeholder: #c0c4cc;

// Border Color
$border-base: #dcdfe6;
$border-light: #e4e7ed;
$border-lighter: #ebeef5;
$border-lightex: #f2f6fc;

$marker-color: $warning-color;
$btn-bg-color: $warning-color;
$btn-tex-color: $text-color;
$btn-padding: 0.625rem 1.25rem;

$bg-footer-color: #1f3244;
$text-footer-color: #ffffff;
$text-news--time-color: #808080;
$link-footer-color-hover: $warning-color;

$bg-form-color: #f7f7f8;
$text-form-color: $text-color;

$padding-and-margin: 1.25rem;

:root {
  --padding-and-margin: 1.25rem;
  // --padding: 0.625rem 1.25rem;
  --padding: 0.72rem 1.25rem 0.7rem;
  --mb: 1.25rem;
  --border-radius: 5px;
  --line-height-num: 3;
  --count-column: 4;
}
