.page-footer {
  padding: 2.5rem 0;
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  background-color: $secondary-color;
  color: $default-color;

  &__container {
    display: flex;
    flex-wrap: wrap;
  }
  &__left {
    flex-grow: 1;

    @include for-size(tablet-portrait-up) {
      flex: 0 0 65%;
    }
  }
  &__right {
    flex-grow: 1;
    margin-top: $padding-and-margin;
    @include for-size(tablet-portrait-up) {
      margin-top: 0;
      flex: 0 0 35%;
    }

    @include for-size(tablet-landscape-up) {
      // margin-left: 5rem;
    }
  }
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__title {
    color: $default-color;
  }
  &__logo-and-copyright {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1.25rem;
  }
  &__copyright {
    @include for-size(tablet-portrait-up) {
      margin-left: 2.5rem;
    }
  }
  &__list {
    @include listReset();
    flex: 0 0 100%;

    @include for-size(tablet-portrait-up) {
      flex: 0 0 50%;
    }
    @include for-size(tablet-landscape-up) {
      flex: 0 0 30%;
    }
  }
  &__list-link {
    line-height: 1.6;
    color: $default-color;
    text-decoration: none;

    &:focus,
    &:hover {
      color: $link-footer-color-hover;
    }
  }
  &__list-item {
    display: flex;
    flex-direction: column;
  }
  &__payment {
    margin-bottom: 1.25rem;
  }
}
