.panel {
  padding: $padding-and-margin 0;

  @include grid-container();
  &__inner {
    @include grid-row();
  }
  &__sidebar {
    @include grid-col(
      (
        xs: 12,
        sm: 3
      )
    );
  }
  &__main {
    max-width: 100%;

    @include grid-col(
      (
        xs: 12,
        sm: 9
      )
    );

    overflow: hidden;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &-item {
      margin-bottom: calc(var(--padding-and-margin) * 2);

      &-text {
        margin-right: var(--padding-and-margin);
        font-family: $mainFont-bold;
        font-weight: $mainFontWeight-bold;
      }
    }
  }
}
