.page-error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: calc(var(--padding-and-margin) * 2) 0;

  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__404 {
    padding: calc(var(--padding-and-margin) / 2) 0;
    @include ff("font-size", $main404);

    line-height: normal;
  }
}
